<template>
  <div class="container-md p-5" style="margin-top: 64px;">
    <h1 class="h3 mb-3 font-weight-normal">新增課程</h1>
    <form class="row justify-content-center" @submit.prevent="postCourse">
      <div class="mb-2">
        <label for="inputTitle" class="sr-only">課程名稱：</label>
        <input
          type="text"
          id="inputTitle"
          class="form-control"
          placeholder="請輸入課程名稱"
          required
          autofocus
          v-model="course.title"
        />
      </div>
      <div class="mb-2">
        <label for="inputContent" class="sr-only">課程內容：</label>
        <textarea
          type="text"
          id="inputContent"
          class="form-control"
          placeholder="請輸入課程內容"
          required
          autofocus
          maxlength="50"
          v-model="course.description"
        />
      </div>
      <div class="mb-2">
        <label for="inputPrice" class="sr-only">價格：</label>
        <input
          type="number"
          id="inputPrice"
          class="form-control"
          placeholder="請輸入價格"
          required
          autofocus
          max="9999"
          v-model="course.price"
        />
      </div>
      <div class="text-end mt-4">
        <button class="btn btn-lg btn-primary btn-block" type="submit">新增課程</button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapState } from 'pinia'
import currentUserStore from '@/stores/currentUserStore.js'

export default {
  data () {
    return {
      course: {
        title: '',
        description: '',
        price: ''
      }
    }
  },
  computed: {
    ...mapState(currentUserStore, ['currentUser'])
  },
  methods: {
    postCourse () {
      const token = JSON.parse(localStorage.getItem('user')).token
      const api = 'https://mevn-curd.onrender.com/api/courses/'
      this.$http.post(api, this.course, {
        headers: {
          Authorization: token
        }
      })
        .then((res) => {
          console.log(res)
          window.alert(res.data.message)
          this.$router.push('/course')
        })
        .catch((e) => {
          console.log(e)
        })
    }
  },
  created () {
    if (this.currentUser.username === undefined) {
      this.$router.push('/login')
    } else if (this.currentUser.role === 'student') {
      this.$router.push('/course')
    }
  }
}
</script>
